<template>
  <div class="dealer-wrap">
    <app-header
      title="经销商中心"
      showBack="true"
    ></app-header>
    <div class="dealer-main">
      <div class="header">
        <span>单位名称：{{dealerInfo.name}}</span>
        <span>编号：{{dealerInfo.code}}</span>
      </div>
      <div class="main">
        <div v-if="dealerInfo.role === 1 || dealerInfo.role === 2">
          <div class="main-item">
            <router-link to="/dealer/verify">核销</router-link>
          </div>
          <div class="main-item">
            <router-link to="/dealer/verify/list">核销列表</router-link>
          </div>
          <div class="main-item">
            <router-link to="/dealer/message">消息中心</router-link>
          </div>
          <div class="main-item">
            <router-link to="/dealer/btob">经销商对经销商</router-link>
          </div>
          <div class="main-item">
            <router-link to="/dealer/modify_pwd">修改密码</router-link>
          </div>
          <div class="main-item">
            <router-link to="/dealer/bind_tel">{{dealerInfo.tel ? '解绑手机' : '绑定手机'}}</router-link>
          </div>
          <div class="main-item">
            <a href="javascript:void(0)" @click="clickLogout">退出登陆</a>
          </div>
        </div>
        <div v-else-if="dealerInfo.role === 3">
          <div class="main-item">
            <router-link to="/dealer/verify/list">核销列表</router-link>
          </div>
          <div class="main-item">
            <router-link to="/dealer/create_account">新增账号</router-link>
          </div>
          <div class="main-item">
            <router-link to="/dealer/account_list">子账号列表</router-link>
          </div>
          <div class="main-item">
            <router-link to="/dealer/dealer_list">经销商列表</router-link>
          </div>
          <div class="main-item">
            <router-link to="/dealer/data">设置数据走向</router-link>
          </div>
          <div class="main-item">
            <router-link to="/dealer/download?type=1">周转报表下载</router-link>
          </div>
          <div class="main-item">
            <a href="javascript:void(0)" @click="clickLogout">退出登陆</a>
          </div>
        </div>
        <div v-else>
          <div class="main-item">
            <router-link to="/dealer/verify/list">核销列表</router-link>
          </div>
          <div class="main-item">
            <a href="javascript:void(0)" @click="clickLogout">退出登陆</a>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import AppHeader from '../../components/header/header.vue'
import {getDealerInfo, logoutDealer} from '../../api/maintain'
import * as types from '../../store/mutation-types'
export default {
  components: { AppHeader },
  name: 'Dealer',
  data () {
    return {
      hexiaoState: false,
      dealerInfo: ''
    }
  },
  created () {
    getDealerInfo()
      .then(data => {
        if (data.state) {
          this.dealerInfo = data.data
          if (this.dealerInfo.role === 1 || this.dealerInfo.role === 2) {
            if (!this.dealerInfo.tel) {
              this.$toast('请先绑定您的手机号')
              this.$router.replace('/dealer/bind_tel')
            }
          }
        } else {
          this.$toast(data.errormsg)
        }
      })
  },
  mounted () {
  },
  methods: {
    clickLogout () {
      this.$messagebox.confirm('确定要退出登陆?')
        .then(action => {
          logoutDealer()
            .then(data => {
              if (data.state) {
                this.$router.replace('/login?type=dealer')
                this.$store.commit(types.CHANGE_DEALER_LOGIN_STATE, false)
              } else {
                this.$toast(data.errormsg)
              }
            })
        })
        .catch(() => {
        })
    }
  }
}
</script>

<style lang="less" scoped>
.dealer-wrap {
  background: #e5e5e5;
  height: 100%;
  overflow: hidden;
  .dealer-main {
    margin-top: 40px;
    .header {
      font-size: 16px;
      font-weight: 600;
      background: #fff;
      box-shadow: 0px 1px 7px rgba(0, 0, 0, 0.6);
      span {
        display: block;
        padding: .5rem 1rem;
      }
    }
    .main {
      margin-top: 1rem;
      background-color: #fff;
      .main-item {
        padding: 0.5rem 1rem;
        border-bottom: 1px solid #eee;
        transition: all 0.5s ease-in-out;
        &:active {
          background: #99ccff;
          color: #fff;
        }
        a {
          display: block;
          color: #333;
        }
      }
    }
  }
}
</style>



